.App {
}

.App-intro {
  font-size: large;
}

.App-left {
   float:left;
}

.App-mid {
   float:left;
}

.App-doc {
   float:left;
}

/* For mobile phones: */
[class*="App-col-"] {
    width: 100%;
    float:left;
    padding: 15px;
}
@media only screen and (min-width: 768px) {
    /* For desktop: */
    .App-col-1 {width: 8.33%;}
    .App-col-2 {width: 16.66%;}
    .App-col-3 {width: 25%;}
    .App-col-4 {width: 33.33%;}
    .App-col-5 {width: 41.66%;}
    .App-col-6 {width: 50%;}
    .App-col-7 {width: 58.33%;}
    .App-col-8 {width: 66.66%;}
    .App-col-9 {width: 75%;}
    .App-col-10 {width: 83.33%;}
    .App-col-11 {width: 91.66%;}
    .App-col-12 {width: 100%;}
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



.App-header {
  background-color: #222;
  height: 190px;
  padding: 18px;
  color: white;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 85px;
}

.docTitle {
  text-align: center;
}


.documentation {
  padding: 10px;
  background-color: #222;
}



.options-panel{
  margin: 0;
  padding: 0;
}



.thr-panel{
  width: 100%;
}



.canvas {
   background:#111;
   border: 1px solid blue;
}

.cheatBox {
   padding: 0px;
   margin: 0px;
}

.slide-box {
   padding: 15px;
   margin: 0px;
}

.machine-form {
   margin: 5;
   padding: 0px;
}

.machineSmaller {
    padding: 4px;
    margin: 0;
}

.finishButton {
    width: 100%;
    padding: 15px;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

